.auth__alert-error {
  background-color: darkred;
  border-radius: 5px;
  color: white;
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
  padding: 5px;
}

.auth__login {
  display: flex;
  height: 100vh;
  background-color: $light-grey;
  flex-direction: column;
  align-items: center;
}
